export const checkSalary = (from: number, to: number, currency: string) => {
    if (from === 0 && to === 0) {
        return '/  '
    }
    if (from > 0 && to > 0) {
        return `/ ${from} - ${to} ${currency}`
    }
    if (from > 0 && to === 0) {
        return `/  ${from} ${currency}`
    }
    if (from === 0 && to > 0) {
        return `/ ${to} ${currency}`
    }
}